export default {
  // 
  state: {
    // 
    balance: '',
    //  ktv
    /** 
      **/
    Signing: false,
    Past: false, // 
    //  -
    Past_btn: false,
    //  
    owner_type: ''

  },
  mutations: {
    // ktv
    Setbalance (state, balance) {
      state.balance = balance
    },
    //  
    SetOwnerType(state, type) {
      state.owner_type = type
    },
    //  
    SigningState(state, Signing) {
      state.Signing = Signing
    },
    //  
    PastSet(state, Past) {
      state.Past = Past
    },
    // 
    PastSetBtn(state, value) {
      state.Past_btn = value
    }
  }
}
