export const addZero = (num) => {
  console.log(num)
  var num = parseInt(num)
  if (num < 10 && num >= 0) {
    return '0' + num
  } else {
    return num
  }
}

/*
  
 */
export const ArrToObj = (arr) => {
  var obj = {}
  for (var i = 0; i < arr.length; i++) {
    for (var key in arr[i]) {
	  obj[key] = arr[i][key]
    }
  }
  return obj
}

