export const NoticeShow = (data, Vm) => {
	Vm.$Notice.config({
    top: 80,
    duration: 3
  })
	Vm.$Notice.info({
								title: data.title,
								name: data.id,
								render: h => {
										return h('span',{
											style: {
												cursor: 'pointer'
											},
											on: {
												click: () => {
													Vm.$Notice.close(data.id)
													if(!!data.url) {
														store.commit('setMessageReadState', data.id)
														Vm.$router.push({
															name: data.url
														})		
													}else {
														Vm.$router.push({
															name: 'message_page'
														})	
													}
													
												}
											}
										}, data.content)
								}
						});
}

