export const getLastMount = () => {
	var date = new Date();
	var year = date.getFullYear();
	var month = date.getMonth();
	if(month == 0){
		year = year - 1;
		month = 12;
	}
	month = month > 10 ? month:'0'+month;
	return year +"-"+ month +"-"+ '01';
}

